<template>
  <div :id="editorId"></div>
</template>

<script>
import merge from 'lodash.merge'

let script
// 
const scriptId = 'onlyoffice-editor'
//  api.js
const loadScript = src => new Promise((resolve, reject) => {
  script = document.querySelector(`#${scriptId}`)
  // 
  const onLoad = () => {
    resolve()
    script.removeEventListener('load', onLoad)
  }
  // 
  const onError = () => {
    reject(new Error(` ${src} `))
    script.removeEventListener('error', onError)
  }
  if (!script) {
    script = document.createElement('script')
    script.id = scriptId
    script.src = src
    script.addEventListener('load', onLoad)
    script.addEventListener('error', onError)
    document.head.appendChild(script)
  } else if (window.DocsAPI) {
    resolve()
  } else {
    script.addEventListener('load', onLoad)
    script.addEventListener('error', onError)
  }
})

export default {
  name: 'onlyoffice-editor',
  props: {
    /**
     * Onlyoffice 
     */
    config: {
      type: Object,
      required: true,
      validator: value => value && Object.prototype.toString.call(value) === '[object Object]' && Object.keys(value).length > 0
    },
    /**
     * Onlyoffice api.js 
     */
    src: {
      type: String,
      required: true
    }
  },
  data () {
    return {
      // https://api.onlyoffice.com/editors/config/
      editorConfig: {
        // 
        width: 1200,
        // 
        height: 600,
        //  wordcellslidePPT
        documentType: 'word',
        // 
        document: {
          // 
          permissions: {
            // 
            comment: false,
            // 
            download: true,
            // 
            edit: true,
            // 
            print: true,
            // 
            review: true
          }
        },
        editorConfig: {
          // 
          callbackUrl: this.src,
          // 
          lang: 'zh-CN',
          // customization https://api.onlyoffice.com/editors/config/editor/customization
          customization: {
            // 
            forcesave: true,
            features: {
              // 
              spellcheck: false
            }
          }
        }
      },
      scriptId,
      editorId: `editor-${new Date().getTime().toString('32')}`
    }
  },
  watch: {
    config: {
      handler () {
        this.initEditor()
      },
      deep: true
    },
    src () {
      this.initEditor()
    }
  },
  mounted () {
    this.initEditor()
  },
  beforeDestroy () {
    // 
    if (this.editor) {
      this.editor.destroyEditor()
      this.editor = null
    }
  },
  methods: {
    // 
    initEditor () {
      loadScript(this.src).then(this.createEditor)
    },
    // 
    createEditor () {
      if (this.editor) {
        this.editor.destroyEditor()
        this.editor = null
      }
      this.editor = new window.DocsAPI.DocEditor(this.editorId, merge({}, this.editorConfig, this.config))
      this.$emit('ready', this.editor)
    }
  }
}
</script>
